@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700&display=swap);
/* Universal Styles */

html {
  height: 100%;
  font-size: 18px;
  background-color: #222;
  font-family: "Noto Sans JP", sans-serif;
}

@media only screen and (max-width: 560px) {
  html {
    font-size: 15px;
  }
}

input:focus {
  outline: none;
}

/* Reusable Component - Image Container */

.image-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  overflow: hidden;
  background: #000;
}

.image-container img {
  width: 100%;
}

/* App Styles */

.App {
  font-family: "Noto Sans JP", "Work Sans", sans-serif;
}

h1 {
  padding: 0.66rem 0;
  text-align: center;
  letter-spacing: 10px;
  background-color: #222;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 300;
  font-size: 1.8rem;
  color: #fff;
}

h6 {
  padding: 1rem 0;
  text-align: center;
  letter-spacing: 5px;
  background-color: #222;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 100;
  font-size: 0.5rem;
  color: #fff;
}

@media only screen and (max-width: 560px) {
  h6 {
    font-size: 0.4rem;
    letter-spacing: 3px;
  }
}

.header {
  height: 3rem;
  margin-bottom: 0.5rem;
}

.header img {
  height: 100%;
  display: block;
  width: auto;
  margin: 10px auto 0 auto;
}

@media only screen and (max-width: 560px) {
  .header {
    height: 2.5rem;
  }
}

.footer {
  color: #fff;
  text-align: center;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 100;
  font-size: 0.5rem;
  margin-bottom: 0.5rem;
  letter-spacing: 3px;
}

.footer small {
  display: block;
  margin-top: 0.5rem;
}

.footer a {
  color: #aaa;
  text-decoration: none;
  letter-spacing: 2px;
}

.footer a:hover {
  color: #fff;
}

@media only screen and (max-width: 560px) {
  .footer small {
    font-size: 0.5rem;
    letter-spacing: 3px;
  }
}

.BusinessList {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 4.4rem 10%;
}
/* Image-container */
.BusinessImage {
  margin-bottom: 1rem;
  box-shadow: 2px 2px 10px #000;
  border-radius: 0.5rem;
}

.BusinessImage img {
  height: 16.66rem;
  width: 16.66rem;
  border-radius: 0.5rem;
  object-fit: cover;
  transition: 0.3s;
  cursor: -webkit-zoom-in;
  cursor: zoom-in;
}

.BusinessImage .normal:hover {
  opacity: 0.7;
}

.dialog {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

.dialog .popup {
  background-color: #fefefe;
  display: block;
  margin: 4rem auto;
  padding: 10px;
  border: 1px solid #888;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 80%;
  max-height: 80%;
  cursor: -webkit-zoom-out;
  cursor: zoom-out;
}

.dialog .close {
  color: #aaa;
  margin: 0 3rem;
  float: right;
  font-size: 2rem;
  font-weight: bold;
}

.dialog .close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

@media only screen and (max-width: 560px) {
  .dialog .popup {
    margin: 15rem auto;
    max-width: 80%;
    max-height: 50%;
  }

  .dialog .close {
    margin: 2rem 1.5rem;
  }
}

.Business {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: #fff;
  font-family: "Noto Sans JP", sans-serif;
  width: 16.66rem;
  margin: 0 0.5rem 2.3rem 0.5rem;
}

.Business h2 {
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.Business-information {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.Business-information p {
  font-size: 0.88rem;
  font-weight: 300;
  line-height: 1rem;
}

.Business-address {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.Business-reviews {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  text-align: right;
}

.Business-reviews h3 {
  color: #fff;
  font-weight: 600;
}

.Business-reviews .rating {
  font-size: 0.88rem;
  line-height: 1rem;
}

.Business a {
  color: #ffffff;
  text-decoration: none;
}

.Business a:hover {
  color: #999;
}

.SearchBar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  background-image: url(/static/media/background_search_desktop.4c970019.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  height: 30rem;
}

.SearchBar-sort-options ul {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 2.22rem;
  color: #ffffff;
}

.SearchBar-sort-options li {
  cursor: pointer;
  width: 4.33rem;
  border-bottom: 1px solid #222;
  padding: 0 2.58rem 1rem 2.58rem;
  line-height: 1.13;
  text-align: center;
  font-weight: 600;
  font-size: 0.83rem;
  transition: color 0.25s;
}

.SearchBar-sort-options li:hover {
  color: #222;
  border-bottom: 1px solid #222;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
  border-bottom: 1px solid #fff;
  color: #fff;
}

@media only screen and (max-width: 560px) {
  .SearchBar-sort-options li {
    padding: 1rem 2.58rem 1rem 2.58rem;
    border-radius: 1px;
    font-size: 0.75rem;
  }

  .SearchBar-sort-options li.active,
  .SearchBar-sort-options li.active:hover {
    background-color: rgba(0, 0, 0, 0.4);
  }
}

.SearchBar-fields {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 2.88rem;
}

.SearchBar-fields input {
  width: 21rem;
  padding: 0.66rem 1rem;
  margin-right: 2.22rem;
  border: 1px solid #222;
  border-radius: 4px;
  font-size: 0.77rem;
  font-weight: 500;
}

.SearchBar-fields input:last-child {
  margin-right: 0;
}

.SearchBar-submit {
  text-align: center;
}

.SearchBar-submit button {
  border-radius: 4px;
  border-color: #000;
  padding: 0.5rem 2rem;
  background-color: #222;
  color: #ffffff;
  font-weight: 600;
  font-size: 1rem;
  transition: background-color 0.5s;
}

.SearchBar-submit button:hover {
  cursor: pointer;
  background-color: #fff;
  color: #222;
}

@media only screen and (max-width: 560px) {
  .SearchBar {
    background-image: url(/static/media/background_search_mobile.cb9c4896.jpg);
    height: 40rem;
  }

  .SearchBar-sort-options ul {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .SearchBar-fields {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }

  .SearchBar-fields input {
    margin-right: 0;
    margin-bottom: 0.86rem;
  }
}

